@media only screen and (max-width: 576px) {
  #main {
    margin: 1.5rem;
    overflow-wrap: break-word; 
  }
}

@media only screen and (min-width: 577px) {
  #main {
    margin: 0.5rem;
    overflow-wrap: break-word; 
  }
}

.container {
  max-width: 100%;
}

.row {
  display: flex;
}

.column {
  flex: 50%;
  padding: 1.5rem 1.5rem;
}

.center-h {
  justify-content: center;
  text-align: center;
}

.center-v {
  align-content: center;
  align-items: center;
}

.display-xl {
  font-size: 5rem;
  font-size: clamp(1.2rem, 12vw, 6rem);
}

.display-lg {
  font-size: 4rem;
  font-size: clamp(1.15rem, 12vw, 5rem);
}

.display-md {
  font-size: 3rem;
  font-size: clamp(1.1rem, 12vw, 3.75rem);
}

.display-sm {
  font-size: 2rem;
  font-size: clamp(1.05rem, 12vw, 2.5rem);
}

.display-xs {
  font-size: 1.5rem;
  font-size: clamp(1rem, 12vw, 2rem);
}

.display-xxs {
  font-size: 1.25rem;
  font-size: clamp(0.9rem, 12vw, 1.5rem);
}

.hide {
  display: none;
}

.relative {
  position: relative;
}

.normal {
  font-weight: normal;
}

.bold {
  font-weight: bold;
}

.highlightDarkBlue {
  color: var(--dark-blue);
  font-weight: bold;
}

.sticky {
  position: sticky;
  top: 2rem;
}