#welcome {
    color: var(--space-cadet);
}

#myPicture {
    display: block;
    margin-left: auto;
    margin-right: auto;
    padding: 0rem;
}

.projectCard a {
    text-decoration: none;
}

@media only screen and (max-width: 768px) {
    #myPicture {
        border-radius: 0.5rem 0.5rem 0.5rem 0.5rem;
    }

    .projects {
        margin: 1rem;
    }
    .projectCard {
        border-radius: 0.25rem;
        margin: 1.5rem 0rem;
    }

    .projectCard .nameBox {
        color: white;
        padding: 0.5rem 1rem;
        box-sizing: border-box;
        border-radius: 0rem 0rem 0.5rem 0.5rem;
    }
    .projectImage {
        height: min(70vw, 20rem);
        width: 100%;
        border-radius: 0.5rem 0.5rem 0rem 0rem;
        padding: 0rem;
        margin: 0rem;
        overflow: hidden;
        position: relative;
        
    }
}

@media only screen and (min-width: 769px) {
    .projects {
        margin: 2rem 7.5rem 7.5rem 7.5rem;
    }
    .projectCard {
        position: relative;
        height: 25rem;
        width: max(100%, 10rem);
        margin: 0rem 0rem 7.5rem 0rem;
    }
    .projectCard .nameBox {
        color: white;
        box-sizing: border-box;
        width: 15rem;
        border-radius: 0.25rem;
        padding: 1.2rem;
        z-index: 5;
        /* positioning */
        -webkit-transform: translateX(-50%);
        transform: translateX(-50%);
        position: absolute;
    }
    .projectImage {
        width: 100%;
        height: 100%;
        border-radius: 0.25rem;
        padding: 0rem;
        margin: 0rem;
        overflow: hidden;
        position: relative;
    }
}


.projectCard .projectTitle {
    color: white;
    margin-top: 0rem;
    text-align: center;
}

.nameBox.bottom-left {
    height: 15rem;
    bottom: -5rem;
    left: 10rem;
}

.rect-bg {
    width: 100%;
    height: 100%;
    position: absolute;
    z-index: 1;
}

.rect-highlight {
    width: 200%;
    height: 100%;
    position: absolute;
    background-color: var(--blue-yonder);
    left: 50%;
    transform: skewX(-45deg);
    z-index: 2;
}

.projectCard .circleShadow {
    height: 7rem;
    width: 20rem;
    border-radius: 50%;
    /* background-color: var(--space-cadet); */

    position: absolute;
    right: 12.5rem;
    top: 75%;
    transform: translateX(50%) translateY(-50%);
    z-index: 3;

}

.rightAutoSizeImg {
    position: absolute;
    right: 12.5rem;
    top: 50%;
    transform: translateX(50%) translateY(-50%);
    max-height: 65%;
    max-width: 100%;
    z-index: 4;
}

.autoSizeImg {
    position: absolute;
    max-height: 80%;
    max-width: 80%;
    z-index: 4; 
    top: 50%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
}
