footer {
    padding: 1.5rem;
    margin-left: -0.5rem;
    display: grid;
    grid-template-columns: repeat(3, auto);
    grid-column-gap: 0.3rem;
    align-items: center;
    line-height: 0;
}
footer p {
  font-family: 'Montserrat', sans-serif;
  font-size: 1rem;
  line-height: 1rem;
  margin: 0; 
  justify-self: center;
  grid-column-start: 2;
}
footer .socials {
  grid-column-start: 3;
  justify-self: end;
}
footer a {
  color: var(--jet-black);
  font-size: 1.75rem;
  display: inline-block;
}

#footerHr1, #footerHr2 {
  border: 0rem;
  height: 0.5rem;
}

#footerHr1 {
  background-color: var(--peach);
}

#footerHr2 {
  background-color: var(--salmon);
}

@media only screen and (max-width: 576px) {
  #footerHr1, #footerHr2 {
    margin: 0rem -1.5rem;
  }
}
  
@media only screen and (min-width: 577px) {
  #footerHr1, #footerHr2 {
    margin: 0rem -0.5rem;
  }
}

