.iconSection {
    padding: 1rem;
}

.iconDisplay {
    display: flex;
    flex: 0 1 1;
    flex-direction: row;
    flex-wrap: wrap;
    gap: 1rem;
    padding: 1rem 0rem;
}

.iconDisplay.spread {
    justify-content: space-between;
}

.iconGroup {
    flex: 0 1 1;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    min-width: 150px;
    min-height: 0;
    max-width: 30%;
}


.linkedIcon {
    height: 4rem;
    width: 4rem;
    color: var(--space-cadet);
    transition: all .2s ease-in-out;
}

.linkedIcon:hover {
    /* height: 5rem;
    width: 5rem; */
    transform: scale(1.25);
    color: var(--salmon);
}