@media only screen and (max-width: 576px) {
    .sectionHeader {
        flex: 30%;
        padding: 1rem;
    }
}

@media only screen and (min-width: 577px) {
    .sectionHeader {
        flex: 30%;
        padding: 1rem 0rem 1rem 2rem;
    }
}

.sectionBody {
    flex: 65%;
    padding: 1rem 0rem 1rem 1rem;
}
.sectionHeader > h2 {
    color: var(--space-cadet);
    font-weight: normal;
}
table {
    table-layout: fixed;
    padding: 0.5rem;
    /* border-spacing: 2rem 0; */
}

@media only screen and (min-width: 577px) {
    table {
        margin: 1rem;
    }
}

th, td {
    padding: 0rem 0.25rem;
}

#featureDescriptions {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    gap: 1rem;
    padding: 0rem 1rem;
}
.featureDescription {
    margin: 1rem 0rem;
    padding: 0.5rem;
}
.featureDescription svg {
   margin-bottom: -1rem;
}
.featureBody {
    width: 20rem;
}
img.halfSize {
    width: 10rem;
    max-width: 50%;
}
img.block {
    display: inline-block;
}

.featureDescription .images {
    display: flex;
    justify-content: center;
}

.featureDescription .images.horizontal {
    display: block;
    justify-content: center;
}

.mockups_brainstorm {
    display: flex;
    flex-wrap: wrap;
    /* justify-content: center; */
    /* min-width: 0rem; */
    flex: auto;
}

.mockup_brainstorm img {
    width: 17.5rem;
    max-width: 100%;
}

.mockup_lofi {
    width: 80%;
}

.demoGifContainer {
    padding: 1rem;
}

.demoGif {
    max-width: 50%;
    border-radius: 1rem;
}

.colorCircle {
    height: 4rem;
    width: 4rem;
    border-radius: 50%;
}

.colorCircle.darkTeal {
    background-color: #34656D;
}

.colorCircle.lily {
    background-color: #C6FFC1;
}
.colorCircle.lightPaleGreen {
    background-color: #EAFFF1;
}
.colorCircle.tan {
    background-color: #FFFBDF;
}

img.titlePic {
    max-width: 100%;
    margin-top: 1rem;
}
