/* :root == html for html documents */
:root {
  /* Case Study Colors */
  --meranti-1: #334443;
  --meranti-2: #34656D;
  --meranti-3: #91B6A4;
  --meranti-4: #C6FFC1;
  --meranti-5: #EAFFF1;
  --meranti-6: #FFFBDF;
  /* new color scheme */
  --salmon: #FF9C9C;
  --peach: #FFC3C3;
  --space-cadet: #1B264F;
  --dark-blue: #274690;
  --blue-yonder: #576CA8;
  --jet-black: #302B27;
  font-size: 16px;
}

/* @font-face {
  font-family: 'sans-serif';
  src: local('Montserrat'), url(./fonts/Montserrat.woff) format('woff');
} */
  /* other formats include: 'woff2', 'truetype, 'opentype',
                            'embedded-opentype', and 'svg' */

img {
  max-width: 100%;
}

h1 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 200;
  font-size: 1.4rem;
  font-size: clamp(1.4rem, 48vh, 1.75rem);
  margin-top: 1rem;
  margin-bottom: 0.5rem;
  color: var(--jet-black);
  position: relative;

}
h2 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 200;
  font-size: 1.3rem;
  font-size: clamp(1.3rem, 45vh, 1.625rem);
  margin-top:0.75rem;
  margin-bottom: 0.25rem;
  color: var(--jet-black);
  position: relative;
}
h3 {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 1.2rem;
  font-size: clamp(1.2rem, 42vh, 1.5rem);
  font-weight: 300;
  margin-top:0.5rem;
  margin-bottom: 0rem;
  color: var(--jet-black);
  position: relative;
}
h4 {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 1.1rem;
  font-size: clamp(1.1rem, 38vh, 1.375rem);
  font-weight: 300;
  margin-top:0.25rem;
  margin-bottom: 0rem;
  color: var(--jet-black);
  position: relative;
}
body, button {
  font-family: 'Nunito Sans', sans-serif;
  font-size: 1rem;
  font-size: clamp(1rem, 35vw, 1.25rem);
  font-weight: 300;
  margin: 0;
  background: #ffffff;
  color: var(--jet-black);
  position: relative;
}
