/* Navbar */
nav  {
  position: relative;
  z-index: 11;
  background-color: white;
  padding: 1.25rem;
  height: 2.5rem;
  display: flex;
  align-items: center;
  flex-direction: row;
  font-size: 1.3rem;
 }

.navBackground {
  position: absolute;
  z-index: 10;
  margin: -1.25rem;
  width: 100%;
  height: 100%;
  background-color: white;
}

@media only screen and (max-width: 768px) {
  nav {
    justify-content: space-between;
  }
}

@media only screen and (min-width: 769px) {
  nav {
    justify-content: left;
  }
}

.navLogo,
.navLink {
  position: relative;
  z-index: 12;
  color: var(--space-cadet);
  cursor: pointer;
  text-decoration: none;
  margin: 0 2rem 0 0;
}

.navLink:hover {
  color: var(--salmon);
}

@media only screen and (max-width: 768px) {
  .dropdown-content {
    min-width: 3.5rem;
  }
  .navLink {
    padding: 1.5rem 2rem;
    text-align: center;
  }

  .navMenu-content {
    position: absolute;
    box-sizing: border-box;
    z-index: 13;
    top: 6rem;
    left: 0rem;
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 1rem;
    background-color: white;
  }
  
}

@media only screen and (min-width: 769px) {
  .dropdown-content {
    position: absolute;
    left: -1.5rem;
    z-index: 9;
    min-width: 3.5rem;
    /* display: none; */
    background-color: white;
  }

  .dropdown-content li {
    padding: 0.3rem 0rem 0rem;
  }
  
  .dropdown-content li:first-child {
    color: var(--dark-blue);
    font-size: 1.25rem;
  }
  .dropdown:hover .dropdown-content {
    display: block;
  }
}
.dropdown {
  position: relative;
  display: inline-block;
}

/* get rid of ul styling */
ul.dropdown-content {
  list-style-type: none;
  padding: 0rem 1rem 1rem;
  margin: 0rem;
}

.dropdown-content hr {
  border: 0px;
  height: 1px;
  background-color: var(--blue-yonder);
  width: 50%;
  margin: auto;
  margin-top: 0.75rem;
  margin-bottom: 0.75rem;
}

.dropdown-content .navLink {
  display: block;
  white-space: nowrap;
  padding: 0rem 0.75rem 0rem;
}

/* NavMenu/smaller screen size styling */

.navMenu {
  position: relative;
  z-index: 12;
  width: 2.5rem;
  height: 2.5rem;
  transition-duration: 0.5s;
  margin: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.iconWrapper {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 0rem;
  margin: 0rem;
}

.icon-E, .icon-X {
  position: absolute;
  left: 0rem;
  width: 2.5rem;
  height: 2.5rem;
  padding: 0rem;
  margin: 0rem;
}

.icon-E {
  top: 0rem;
}

.icon-X {
  top: 1.25rem;
}

.bar-horizontal {
  width: 100%;
  height: 1px;
  margin: 1.15625rem 0rem;
  border: 0px;
  background-color: black;
}

.icon-E > .bar-horizontal:first-child {
  margin: 0rem 0rem 1.15625rem;
}

.icon-E > .bar-horizontal:last-child {
  margin: 1.15625rem 0rem 0rem;
}

.bar-diagonal {
  width: 100%;
  height: 1px;
  margin: 0rem;
  border: 0px;
  background-color: black;
}

.bar-diagonal.left {
  transform: rotate(45deg);
}

.bar-diagonal.right {
  transform: rotate(-45deg);
}

#navHr1 {
  border: 0rem;
  height: 0.5rem;
  background-color: var(--salmon);
  margin: 0rem;
}

#navHr2 {
  border: 0rem;
  height: 0.5rem;
  background-color: var(--peach);
  margin: 0rem;
}

.background-darken {
  position: fixed;
  z-index: 9;
  top: 6rem;
  left: 0rem;
  width: 100%;
  height: 100%;
  background-color: black;
  visibility: hidden;
}